
@import "../../abstracts/Variables/Variables.scss";   


.List {
  height: 100%;
  background: rgb(252, 252, 252); 
  
  padding: 20px;


  border-radius: 10px;
  box-shadow: 10px 5px 5px rgba(170, 170, 170, 0.479);

  & h1 {
  font-size: $headingFont;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
    
  }
  
}

.ListMain {
  padding: 10px;
  margin-top: 10px;
}


  .items {
    display: flex;
    justify-content: space-around;
    align-items: center;


    padding: 7px;
    border: 0px;
    background: #F9F9F9;
    vertical-align: middle;
  }

  .items-side {
    padding: 8px;

    & h3 {
      font-size: 16px;
    }

    & a {
    color: $base-color;  
    }

    & a:hover {
      color: #c01a30d0;  
      }
  }
