
@import "../../abstracts/Variables/Variables.scss";   


.Sales {

    padding: 20px;
    // width: 540px;
    border-radius: 10px;
    box-shadow: 10px 5px 5px rgba(170, 170, 170, 0.479);
    
    & h2 {
        font-family: 'Montserrat', sans-serif;
        font-size: $headingFont;  
        font-weight: 700;
    } 

    & h6 {
        font-size: 16px;
        font-weight: 300;
    }
      
    background-color: rgb(252, 252, 252);
    &-text {
        color: $base-color;  
    }
}
