@import "../../abstracts/Variables/Variables.scss";   


.container {

     &_head {
        font-size: $headingFont;
        font-family: 'Montserrat', sans-serif;
        font-weight: 700;
     }
}
.weeksDetails {
display: flex;
// justify-content: space-between;
margin-left: 6px;
align-items: center;

& .thisWeek h4 {
        
        color: $base-color;
}


& .PrevWeek h4 {
    margin-left: 60px;
    color: $base-color;
}

& .thisWeek h6 {
    font-size: 20px;
    font-weight: 300;
}

& .PrevWeek h6 {
    margin-left: 60px;
    font-size: 16px;
    font-weight: 300;
}

}

.upcoming-events {
    background: rgb(247, 247, 247);
    padding: 12px;
    margin-bottom: 40px;
    width: 550px;

    border-radius: 10px;
    box-shadow: 10px 5px 5px rgba(170, 170, 170, 0.479);


    & .table {
        margin-bottom: 0px;
    }

    & tr {
        border-radius: 15px;
        border-bottom: 15px solid #fff;
    }

    & tr td {
        padding: 7px;
        border: 0px;
        background: #F9F9F9;
        vertical-align: middle;
    }

    & tr td img {
        max-width: 30px;
    }

    & tr td a {
        display: block;
        color: #e53632;
        font-size: 12px;
    }
}