.card-body {
  background: rgb(247, 247, 247);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 10px 5px 5px rgba(170, 170, 170, 0.479);
}

.card-body h4 {
  font-size: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

.ticketContainer_circle {
  position: relative;
  text-align: center;
}

.ticketContainer_circle-text {
  text-align: center;
  position: absolute;
  top: 40px;
  left: -30px;
  right: 0px;
  /* background: red; */
}

.ticketContainer_circle-text h2 span {
  font-size: 22px;
}

.ticketContainer_details {
  padding-top: 30px;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  height: 150px;
}

.ticketContainer_details-seat1 h4 {
  color: #e53632;
}

.ticketContainer_details-seat2 h4 {
  color: #e53632;
