.List {
  height: 100%;
  background: rgb(252, 252, 252);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 10px 5px 5px rgba(170, 170, 170, 0.479);
}
.List h1 {
  font-size: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

.ListMain {
  padding: 10px;
  margin-top: 10px;
}

.items {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 7px;
  border: 0px;
  background: #F9F9F9;
  vertical-align: middle;
}

.items-side {
  padding: 8px;
}
.items-side h3 {
  font-size: 16px;
}
.items-side a {
  color: #e53632;
}
.items-side a:hover {
  color: rgba(192, 26, 48, 0.8156862745);
}/*# sourceMappingURL=ListComp.css.map */