:root {
    --blue: #f0f0f0; 
    --white: #ffffff;
  }
  
  * {
    margin: 0%;
    padding: 0%; 
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit; 
  } 
  
  html {
    box-sizing: border-box;
    /* font-size: 62.5%;  */
    } 
  
  hr {
    color: var(--color-white); 
    height: 1px;
    margin: 2rem 8rem 2rem 8rem; 
  }
  
  a {
    text-decoration: none;
    color: #333; 
  }
  
  ul {
    list-style: none; }
  
    body { 
      background-color: var(--blue);
     } 
  