.menu {
  background: #18212c;
}

.topbar-nav {
  background: #18212C;
}
.topbar-nav ul {
  list-style: none;
}
.topbar-nav .metismenu {
  display: flex;
  flex-direction: row;
}
.topbar-nav .metismenu li {
  flex: 1 1 0%;
  display: flex;
  flex-direction: column;
  position: relative;
}
.topbar-nav .metismenu li a {
  position: relative;
  display: block;
  font-size: 14px;
  padding: 27px 0px;
  color: #fff;
  outline-width: 0;
  transition: all 0.3s ease-out;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
}
.topbar-nav .metismenu li a span {
  display: block;
  margin-bottom: 10px;
  font-size: 18px;
}/*# sourceMappingURL=Navbar.css.map */